.nav {
  display: flex;
  position: relative;
  flex-direction: column;
  text-align: center;
  background-color: #2c2c2c;
  border-right: 1px solid;
  border-color: #363636;
  height: 100vh;
  width: 20vh;
  box-shadow: 4px 0px 6px rgba(0, 0, 0, 0.15);
}
.nav .nav-logo {
  display: flex;
  width: 100%;
  height: 5vh;
  justify-content: center;
  align-items: center;
}
.nav .nav-link {
  display: flex;
  width: 100%;
  height: 4vh;
  justify-content: center;
  align-items: center;
}
.nav .nav-link:hover {
  background-color: #262626;
}
.nav .nav-footer {
  display: flex;
  border-top: 1px solid;
  border-color: #363636;
  position: absolute;
  align-items: center;
  bottom: 0px;
  height: 5%;
  width: 100%;
  overflow: hidden;
}
.nav .nav-footer .nav-footer-user {
  display: flex;
  position: relative;
  text-align: left;
  align-items: center;
  width: calc(100% - 25px - 15px);
  margin-left: 10px;
  overflow: hidden;
}
.nav .nav-footer .nav-footer-user .nav-footer-avatar {
  width: 25px;
  height: 25px;
  margin-right: 5px;
}
.nav .nav-footer .nav-footer-user .nav-footer-username {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}
.nav .nav-footer .nav-footer-logout-button {
  cursor: pointer;
  position: absolute;
  background-color: transparent;
  border: 0px;
  background-image: url("../../public/static/images/icons/logout_icon.png");
  background-repeat: no-repeat;
  background-position: center;
  width: 20px;
  height: 20px;
  background-size: 20px;
  right: 5px;
}

.error-page {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}
.error-page .error-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.error-page .error-box .error-header {
  font-family: "Arial Black", "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-weight: 500;
  font-size: 160px;
}
.error-page .error-box .error-content {
  font-size: x-large;
  margin-bottom: 15px;
}
.error-page .error-box .error-home-button {
  background-color: rgb(3, 121, 3);
  padding: 5px 10px;
  border-radius: 4px;
  font-size: large;
}

.pieces {
  position: absolute;
  z-index: 10;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
}

.piece {
  cursor: grab;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 80px;
  width: 90px;
  height: 90px;
}
.piece:active {
  cursor: grabbing;
}

