.chess-board {
  position: relative;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #262626;
  overflow: hidden;
}
.chess-board .disable-board {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 50;
}
.chess-board .disable-board .disable-board-content {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  background-color: rgb(13, 146, 13);
  width: 175px;
  height: 50px;
  border-radius: 25px;
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.2);
}
.chess-board .board-cols {
  font-weight: 500;
  font-size: large;
  position: absolute;
  display: grid;
  grid-template-columns: repeat(8, 90px);
  bottom: 0px;
  left: 75px;
}
.chess-board .board-rows {
  font-weight: 500;
  font-size: large;
  position: absolute;
  display: grid;
  grid-template-rows: repeat(8, 90px);
  top: 0px;
  left: 2px;
}
.chess-board .black-letter {
  color: rgb(119, 153, 84);
}
.chess-board .white-letter {
  color: rgb(233, 237, 204);
}
.chess-board .board-squares {
  display: grid;
  grid-template-columns: repeat(8, 90px);
  grid-template-rows: repeat(8, 90px);
}
.chess-board .board-squares .black-square {
  background-color: rgb(119, 153, 84);
  border-color: rgb(208, 219, 195);
}
.chess-board .board-squares .white-square {
  background-color: rgb(233, 237, 204);
  border-color: rgb(247, 249, 237);
}

.game-container {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.game-container .game-content {
  display: flex;
  flex-direction: column;
  border: 1px solid;
  border-color: #262626;
  border-radius: 10px;
  height: 800px;
  width: 720px;
  overflow: hidden;
  box-shadow: 6px 6px 8px rgba(0, 0, 0, 0.2);
}
.game-container .game-content .game-sidebar {
  font-size: large;
  display: flex;
  align-items: center;
  width: calc(100% - 5px - 5px);
  height: 40px;
  padding: 0px 5px 0px 5px;
  background-color: #2f2f2f;
}
.game-container .game-content .game-sidebar .game-timer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 55px;
  height: 30px;
  background-color: #5c5c5c;
  border-radius: 5px;
  margin-left: auto;
}
.game-container .game-content .game-sidebar .game-user-avatar {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}
.game-container .game-content .game-sidebar .game-user-rating {
  color: grey;
  margin-left: 5px;
}

.chat {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 350px;
}
.chat .chat-container {
  position: relative;
  background-color: rgba(0, 0, 0, 0.1);
  height: 90%;
  width: 85%;
  border-radius: 25px;
  border: 1px solid;
  border-color: #363636;
  overflow: hidden;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.2);
}
.chat .chat-container .chat-header {
  font-size: large;
  display: flex;
  height: 50px;
  justify-content: center;
  align-items: center;
  background-color: #262626;
  border-bottom: 1px solid;
  border-color: #363636;
}
.chat .chat-container .chat-messages {
  width: 100%;
  height: 203px;
  padding: 5px;
  overflow-y: auto;
  overflow-x: hidden;
  word-break: break-all;
}
.chat .chat-container .chat-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0px;
  width: 100%;
  padding: 8px 0px;
  border-top: 1px solid;
  border-color: #363636;
  background-color: #262626;
}
.chat .chat-container .chat-footer .chat-footer-input {
  color: white;
  font-size: medium;
  background-color: #2c2c2c;
  margin-right: 5px;
  border: 1px solid;
  border-color: #363636;
  border-radius: 25px;
  padding: 5px;
}
.chat .chat-container .chat-footer .chat-footer-input:focus {
  outline: 1px solid;
  outline-color: #454545;
  border: 1px solid;
  border-color: #454545;
}
.chat .chat-container .chat-footer .chat-footer-submit {
  cursor: pointer;
  width: 35px;
  height: 35px;
  background-color: transparent;
  border: 0px;
  background-image: url("../../public/static/images/icons/send_message_icon.png");
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}
.chat .chat-container .chat-footer .chat-footer-submit:hover {
  background-color: #2d2d2d;
}

