.form-page .form-container .form .form-input-container .form-input {
  color: white;
  background-color: #313131;
  border: 1px solid;
  border-radius: 25px;
  padding: 8px;
  width: 60%;
  margin-top: 5px;
  box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.2);
