}

.alert {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 150px;
  pointer-events: none;
  animation: reverse-transformer 0.6s ease-out 3s 1;
}
@keyframes reverse-transformer {
  from {
    transform: translate(0px, 0px);
  }
  to {
    transform: translate(0px, -150px);
  }
}
.alert .alert-box {
  font-size: large;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(77, 184, 93);
  position: absolute;
  border: 1px solid;
  border-radius: 25px;
  border-color: rgb(65, 128, 75);
  padding-right: 25px;
  padding-left: 15px;
  height: 50px;
  pointer-events: all;
  box-shadow: 3px 3px 6px rgba(0, 0, 0, 0.2);
  animation: transformer 0.6s ease-out 0s 1;
}
@keyframes transformer {
  from {
    transform: translate(0px, -150px);
  }
  to {
    transform: translate(0px, 0px);
  }
}
.alert .alert-box .alert-icon {
  width: 30px;
  height: 30px;
  margin-right: 5px;
}

